"use client";

import { FC, useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import Image from "next/image";
import logo from '@/public/logo.png';
import Link from 'next/link';
import { ProjectInquiryButton } from "./ProjectInquiryButton";
import { LanguageDropdown } from "./LanguageDropdown";
import '@/app/NavBar.css';
import { Language } from "@/lib/i18n/settings";
import { useTranslation } from "@/lib/i18n/client";

interface Props {
  lng: Language;
}

export const NavBar: FC<Props> = ({ lng }) => {
  const { t } = useTranslation(lng, 'common');
  const [isClient, setIsClient] = useState(false);
  const router = useRouter();

  useEffect(() => {
    setIsClient(true);
  }, []);

  const scrollToSection = (sectionId: string) => {
    const section = document.getElementById(sectionId);
    if (section) {
      section.scrollIntoView({ behavior: 'smooth' });
    }
  };

  const handleProjectInquiryClick = () => {
    router.push("/quotation");
  };

  return (
    <nav className="fixed w-full bg-[#0d3388] backdrop-blur-sm z-50 transition-transform duration-300">
      <div className="max-w-full flex items-center justify-between h-20 px-4">
        <div className="flex-shrink-0">
          <Link href="/" passHref legacyBehavior>
            <Image src={logo} alt="Logo" width={200} height={200} />
          </Link>
        </div>

        <div className="flex items-center justify-center flex-grow">
          <button
            onClick={() => scrollToSection('whylomtech')}
            className="nav-link nav-link-custom"
          >
            {t('whyLomtech')}
          </button>

          <button
            onClick={() => scrollToSection('portfolio')}
            className="nav-link nav-link-custom"
          >
            {t('portfolios')}
          </button>

          <button
            onClick={() => scrollToSection('partners')}
            className="nav-link nav-link-custom"
          >
            {t('partnersi')}
          </button>

          <button
            onClick={() => scrollToSection('contact')}
            className="nav-link nav-link-custom"
          >
            {t('contactUs')}
          </button>
        </div>

        <div className="flex items-center space-x-2">
          {isClient && (
            <div className="transition-opacity duration-300 opacity-100">
              <ProjectInquiryButton onClick={handleProjectInquiryClick} variant="primary">
                {t('projectInquiry')}
              </ProjectInquiryButton>
            </div>
          )}
          <LanguageDropdown />
        </div>
      </div>
    </nav>
  );
};
